  export const Input = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text" : "form-text";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    return (
        <input className={classNames} type="text" placeholder={placeholder} name={name} />
    );
  }

  export const Textarea = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text" : "form-text";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    return (
        <textarea className={classNames}  placeholder={placeholder} name={name}></textarea>
    );
  }

  export const Button = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-button" : "form-button";
    let type = props.hasOwnProperty('type') ? props.type : "";
    return (
      <button type={type} className={classNames} onClick={props.onClick}>
        {props.text}
      </button>
    );
  }
  
  export const InputGroupButton = (props) => {
    let classNames = props.hasOwnProperty('className') ? props.className+" "+"form-text-group" : "form-text-group";
    let classNamesButton = props.hasOwnProperty('className') ? props.className+" "+"form-button-group" : "form-button-group";
    let placeholder = props.hasOwnProperty('placeholder') ? props.placeholder : "";
    let name = props.hasOwnProperty('name') ? props.name : "";
    let type = props.hasOwnProperty('type') ? props.type : "";
    return (
      <div className="input-group">
        <input className={classNames} type="text" placeholder={placeholder} name={name} onChange={props.setInputValue}/>
        <button type={type} className={classNamesButton} onClick={props.onClickButton}>
          {props.text}
        </button>
      </div>
    );
  }


  export const Select = (props) => {
    const openSelect = useCallback((e) => {
      if(!e.target.parentNode.querySelector('.in-select-options').classList.contains('show')){
        e.target.parentNode.querySelector('.in-select-options').classList.add('show');
      }
      else{
        e.target.parentNode.querySelector('.in-select-options').classList.remove('show');
      }
      
    });
    const handleClick = useCallback((e, item) => {
      //{Object.keys(props.options).map((item, key)=><li key={item} onClick={(e) => handleClick(e, item)} className="in-select-option">{props.options[item]}</li>)}
      //Array.from(e.target.parentNode.childNodes).forEach((el) => el.classList.remove('active'));
      //Array.from(document.querySelectorAll('.in-select-option')).forEach((el) => el.classList.remove('active'));
      //if(!e.target.classList.contains('active')){
        //e.target.classList.add('active');
      //}
      const closest = e.target.closest('.in-select-options');
      closest.classList.remove('show');
      const parent = e.target.closest('.in-select');
      parent.querySelector('.in-select-tag').value = item.key;
      Array.from(e.target.parentNode.childNodes).forEach((el) => el.classList.remove('active'));
      e.target.classList.add('active');
      props.onChange(item);
      
    })
    return (
      <section className="in-select">
        <select className="in-select-tag" name={props.name} style={{display: 'none'}}>
        <option key='empty' value="">{props.defaultValue}</option>
          {props.options.map((item, key)=><option key={item.key} value={item.key}>{item.value}</option>)}
        </select>
        <div className="in-select-value" onClick={openSelect}>{props.selectedValue != null ? props.selectedValue.value : props.defaultValue}</div>
        
        <div className="in-select-options">
            <ul>
            <li key="empty" onClick={(e) => handleClick(e, {value:props.defaultValue, key: ''})} className="in-select-option">{props.defaultValue}</li>
            {props.options.map((item, key)=><li key={item.key} onClick={(e) => handleClick(e, item)} 
            className={item.key === props.selectedValue.key ? "in-select-option selected" : "in-select-option"}
            >{item.value}</li>)}
            </ul>
        </div>
        <svg  onClick={openSelect} className="in-select-arrow" xmlns="http://www.w3.org/2000/svg" fill="currentColor" width="100%" height="100%" viewBox="0 0 24 24"><g data-name="Layer 2"><g data-name="chevron-down"><rect width="24" height="24" opacity="0"></rect><path d="M12 15.5a1 1 0 0 1-.71-.29l-4-4a1 1 0 1 1 1.42-1.42L12 13.1l3.3-3.18a1 1 0 1 1 1.38 1.44l-4 3.86a1 1 0 0 1-.68.28z"></path></g></g></svg>
      </section>
    );
  }