import { 
  GET_HOME_DATA, 
  GET_BLOG_DATA,
  GET_STATES, 
  GET_DISTRICTS,
  SET_STATE,
  SET_DISTRICT,
  SET_DATE,
  SET_SLOTS,
  SET_CENTER_INFO,
  SET_PORTFOLIO
} from './actions';

const initialState = {
  homeData: {top:[], topics: []},
  blogData: {},
  states: [],
  districts: [],
  qDate: new Date(),
  qStateId: {key:'', value: 'Select State'},
  qDistrictId: {key:'', value: 'Select District'},
  centers: [],
  currentCenter: {sessions:[]},
  portfolio: false
};

function reducer(state = initialState, action) {
  switch(action.type) {  
    case SET_PORTFOLIO:
      return {
        ...state,
        portfolio: action.payload
      };
    case GET_HOME_DATA:
      return {
        ...state,
        homeData: action.payload
      };
    case GET_BLOG_DATA:
      return {
        ...state,
        blogData: action.payload
      };  
      case GET_STATES:
        return {
          ...state,
          states: action.payload
        };
      case GET_DISTRICTS:
        return {
          ...state,
          districts: action.payload
        };  
      case SET_STATE:
        return {
          ...state,
          qStateId: action.payload
        };  
      case SET_DISTRICT:
        return {
          ...state,
          qDistrictId: action.payload
        };
      case SET_SLOTS:
        return {
          ...state,
          centers: action.payload
        };  
      case SET_DATE:
        return {
          ...state,
          qDate: action.payload
        };
      case SET_CENTER_INFO:
        return {
          ...state,
          currentCenter: action.payload
        };
      default:
        return state;
  
    }
  }
  export default reducer;
